import {mapGetters,mapActions} from 'vuex';
import {showToast,setLocalStorage} from '@/utils/index';
export default{
	props: {
		books: {
			default: []
		}
	},
	data(){
		return{
			
		}
	},
	computed: {
		...mapGetters('novel', ['bookStore']),
	},
	methods: {
		jumpPage(url){
			if(url && typeof(url)=='string'){
				uni.navigateTo({
					url: url,
					animationType: 'slide-in-bottom'
				});
			}
		}
	}
}
